import styled, { css } from "styled-components";

import LinkButton from "../Button/LinkButton";
import { colors, Margins, marginsCss, spacingMap } from "../styleguide";

import { FileUploaderWrapperProps } from "./FileUploader.types";

const hoverAndDragCss = css<FileUploaderWrapperProps>`
	background: linear-gradient(0deg, rgba(61, 85, 204, 0.05), rgba(61, 85, 204, 0.05)), ${colors.white};
	border: 2px solid ${({ showValidationError }) => showValidationError ? colors.destructive : colors.midBlue};
	border-radius: 10px;

	svg {
		stroke: ${colors.midBlue}
	}
`;

export const FileUploaderWrapper = styled.div<Margins>`
	${marginsCss};
`;

export const StyledUploaderWrapper = styled.div<FileUploaderWrapperProps>`
	display: flex;
	position: relative;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;
	border: 2px solid ${({ showValidationError }) => showValidationError ? colors.destructive : colors.lightGray};
	border-radius: 10px;
	padding: ${spacingMap.lg};
	background-color: ${colors.white};
	
	${({ dragActive, uploadButtonIsFocused }) => (dragActive || uploadButtonIsFocused) && hoverAndDragCss};
	:hover {
		${({ isDisabled }) => isDisabled ? "cursor: not-allowed;" : hoverAndDragCss}
	}

	${({ isDisabled }) => isDisabled && `
		background-color: ${colors.lightestGray};
		border: 2px solid transparent;
	`};
`;

export const UploadButton = styled(LinkButton)`
	background: transparent;

	// shadow is not needed since background changes and text is underlined
	:focus {
		box-shadow: none;
	}

	:disabled {
		opacity: 100%;
		> span {
			color: ${colors.black}A8;
		}
	}
`;

export const FileUploaderTitle = styled.div`
	display: flex;
	margin-top: ${spacingMap.xs}
`;

export const FileUploaderInput = styled.input`
	display: none;
`;

export const DragFileElement = styled.div`
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: 10px;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
`;

export const ErrorMessageWrapper = styled.div`
	display: flex;
	flex-direction: row;
	gap: ${spacingMap.xxs};
	margin-top: ${spacingMap.xxs};
`;
